import React from 'react';

import VotingCard from '../VotingCard';

export default {
  title: 'Voting/VotingCard',
  component: VotingCard,
  argTypes: {
      title: '',
    state: {
      options: ['open', 'close'],
      control: { type: 'radio' }
    },
    votes: []
  }
};

const Template = (args) => <VotingCard {...args} />;

export const Voting1 = Template.bind();;
Voting1.args = {
    title: 'Do you agree?',
    state: 'open',
    votes: [{option:'Yes', count:8}, {option:'No',count:3}]
}

export const Voting2 = Template.bind();
Voting2.args = {
    title: 'Do you want ice cream?',
    state: 'open',
    votes: [{option:'Yes', count:8}, {option:'No', count:3}, {option:'Maybe', count:5}]
}


